@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap");
:root {
  --red: #b92222;
  --white: #fff;
  --black: #0f172a;
}

:root {
  --font-Sans: "IBM Plex Sans", sans-serif;
  --font-Poppins: "Poppins", sans-serif;
  --font-Normal: 400;
  --font-semiBold: 600;
  --font-Bold: 700;
}

@keyframes headerScrollIn {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes headerScrollOut {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}
.section__select {
  transition: all 650ms ease-in-out;
}
.section__hidden {
  opacity: 0;
  transform: translateY(8rem);
  transition: all 650ms ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

body {
  position: relative;
  font-family: var(--font-Poppins);
  font-size: 16px;
  overflow-x: hidden;
}
@media screen and (max-width: 768px) {
  body.no-scroll {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
}

ul,
ol {
  list-style: none;
}

button,
input {
  font-family: var(--fontNormal);
}

a,
a:visited,
a:active {
  text-decoration: none;
}
a:focus,
a:visited:focus,
a:active:focus {
  outline: none;
}
a:focus-visible,
a:visited:focus-visible,
a:active:focus-visible {
  outline: 2px solid var(--red);
  outline-offset: 3px;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.container {
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
}

.skip-to-content-link {
  position: fixed;
  top: 110px;
  left: 0;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  padding: 20px 40px;
  background-color: var(--white);
  font-size: 15px;
  text-decoration: underline;
  z-index: 100;
  transform: translateX(-200px);
  transition: transform 300ms ease;
  color: var(--black);
}
.skip-to-content-link:focus {
  outline: 3px dashed var(--red);
  outline-offset: 0;
  transform: translateX(0);
}

.loading *,
.loading *::before,
.loading *::after {
  animation-play-state: paused !important;
}

h1,
h2,
h3,
h4,
p {
  font-family: var(--font-Sans);
}

.heading, .heading--xxl {
  color: var(--black);
  font-weight: 400;
}
.heading__name {
  color: var(--red);
}
.heading--intro {
  color: var(--black);
  font-size: clamp(30px, 5vw, 42px);
  text-align: center;
  font-weight: 600;
  margin-bottom: 18px;
}
.heading--xxl {
  font-size: clamp(30px, 7vw, 38px);
  max-width: 450px;
  font-weight: 700;
  margin-bottom: 5px;
  text-transform: uppercase;
}
.heading--xl {
  position: relative;
  font-size: clamp(17px, 4vw, 23px);
  font-weight: 700;
  text-align: center;
  color: var(--red);
  margin-bottom: 29px;
  text-transform: uppercase;
}
.heading--xl::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -5px;
  height: 5px;
  width: 64px;
  margin: 0 auto;
  background-color: var(--red);
}
.heading--l {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  color: var(--black);
  margin-bottom: 19px;
  text-align: center;
}
.heading--m {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 14px;
  color: var(--white);
}
.heading--services {
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  margin: 15px 0;
}
.heading--footer {
  font-size: 40px;
  color: var(--white);
}

.paragraph, .paragraph--l {
  line-height: 1.2;
  color: #444452;
}
.paragraph__name {
  font-weight: 700;
}
.paragraph--l {
  font-size: clamp(20px, 3vw, 21px);
  margin-bottom: 26px;
}
.paragraph--about {
  font-size: 15px;
  margin-bottom: 17px;
  line-height: 24px;
}

.learnMore__text {
  color: var(--red);
  font-weight: 700;
  font-size: 17px;
  letter-spacing: 1px;
  margin-right: 18px;
  transition: margin-right 0.3s ease-in-out;
}
.learnMore__icon svg path {
  fill: var(--red);
}

.btn, .btn--start, .btn--blue, .btn--menu {
  position: relative;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 300ms, border 300ms ease-in;
}
.btn:focus, .btn--start:focus, .btn--blue:focus, .btn--menu:focus {
  outline: none;
}
.btn:focus-visible, .btn--start:focus-visible, .btn--blue:focus-visible, .btn--menu:focus-visible {
  outline: 3px solid var(--red);
  outline-offset: 3px;
}
.btn--menu {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  background-color: transparent;
  z-index: 10;
}
.btn--menu:focus {
  outline: none;
}
.btn--menu:focus-visible {
  outline: 3px solid var(--red);
  outline-offset: 2px;
}
.btn--menu--bar {
  background-color: var(--black);
  display: block;
  height: 0.25rem;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  width: 1.5rem;
}
.btn--menu--bar:not(:last-of-type) {
  margin-bottom: 0.2rem;
}
.btn--menu.menu-active .btn--menu--bar {
  background-color: var(--white);
}
.btn--menu.menu-active .btn--menu--bar:nth-child(1) {
  transform: rotate(45deg) translate(2px, 5px);
  transition-delay: 150ms;
}
.btn--menu.menu-active .btn--menu--bar:nth-child(2) {
  opacity: 0;
}
.btn--menu.menu-active .btn--menu--bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -8px);
  transition-delay: 150ms;
}
@media screen and (min-width: 769px) {
  .btn--menu {
    display: none;
  }
}
.btn--blue {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white);
  box-shadow: 0px 10px 15px -7px var(--black);
  z-index: 1;
}
.btn--blue:hover {
  color: var(--red);
}
.btn--blue:hover::before {
  transition: all 350ms ease;
  background-color: var(--white);
}
.btn--blue::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--red);
  display: block;
  border: 2px solid var(--red);
  border-radius: 4px;
  z-index: -1;
  transition: all 350ms ease;
}
.btn--start {
  width: 180px;
  height: 48px;
}
.btn--more:hover .learnMore__text {
  margin-right: 26px;
}
.btn--view {
  font-size: 16px;
  color: var(--black);
}

.icon__skills {
  color: var(--red);
  font-size: clamp(22px, 4vw, 29px);
}

.icon__project {
  margin-right: 10px;
}

.header {
  position: absolute;
  left: 0;
  width: 100%;
  padding: 24px 28px 0;
  z-index: 999;
}
.header.header--in {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 12px 28px;
  transition: all 0.3s ease-in-out;
  background-color: var(--white);
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.04);
  animation: headerScrollIn 700ms ease-in-out forwards;
}
.header.scroll-out {
  animation: headerScrollOut 300ms ease-in-out forwards;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 51px;
  transition: all 350ms ease-in-out;
  max-width: 740px;
}
.header__logo {
  z-index: 1;
  font-size: 29px;
  color: var(--black);
}
@media screen and (max-width: 768px) {
  .header__logo.active-color {
    color: var(--white);
  }
}
.header__logo--letter {
  font-size: 30px;
  color: var(--red);
}
@media screen and (max-width: 768px) {
  .header__menu {
    position: fixed;
    width: 100%;
    top: 0px;
    right: -1000px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--black);
    transition: all 340ms ease-in;
  }
  .header__menu.is-active {
    right: 0px;
  }
}
.header__list {
  display: flex;
  align-items: center;
}
@media screen and (min-width: 769px) {
  .header__item:not(:last-child) {
    margin-right: 28px;
  }
}
@media screen and (max-width: 768px) {
  .header__list {
    display: grid;
    grid-template-columns: max-content;
    grid-template-rows: repeat(5, 3.75rem);
    align-items: center;
    justify-items: center;
  }
}
.header__link {
  position: relative;
  font-size: 17px;
  font-weight: 600;
  color: var(--white);
  text-transform: uppercase;
}
.header__link:active {
  outline: 3px solid var(--white);
  outline-offset: 3px;
}
.header__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  height: 4px;
  width: 100%;
  background-color: var(--red);
  visibility: hidden;
  opacity: 0;
  transform-origin: left;
  transform: scaleX(0);
}
@media screen and (min-width: 769px) {
  .header__link {
    color: var(--black);
    font-size: 14px;
  }
  .header__link:hover {
    color: var(--red);
  }
  .header__link:hover::before {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
    transition: all 350ms ease-in;
  }
}

.hero {
  position: relative;
  padding: 216px 28px 80px;
}
.hero__container {
  position: relative;
  display: flex;
  flex-direction: column-reverse;
}
@media screen and (min-width: 769px) {
  .hero__container {
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: center;
  margin-top: 25px;
}
@media screen and (min-width: 769px) {
  .hero__text {
    align-items: flex-start;
    text-align: left;
    margin-top: 0;
  }
}
.hero__image {
  position: relative;
  z-index: 5;
  align-self: flex-start;
}
.hero__image img {
  position: relative;
  object-fit: cover;
  height: 100%;
  width: 100%;
  transition: all 0.3s;
}
@media screen and (max-width: 768px) {
  .hero__image img {
    transform: translateY(0);
  }
}
.hero__icon {
  display: flex;
  margin-top: 60px;
}
.hero__icon__link {
  margin: 0 6px;
  transition: all 350ms ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero__icon__link:hover svg path {
  transition: all 350ms ease-in-out;
  fill: var(--red);
}
.hero__fig {
  height: 135px;
  width: 135px;
  padding: 5px;
  border-radius: 1rem;
  box-shadow: 0 10px 15px -7px rgba(0, 0, 0, 0.215);
}
.hero__fig img {
  border-radius: 1rem;
}

.about {
  padding: 80px 28px 20px;
}
.about__container {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  gap: 14px;
}
@media screen and (min-width: 769px) {
  .about__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "about about" "profile skill";
    grid-template-rows: auto;
  }
}
.about__img {
  width: 286px;
  height: 270px;
  object-fit: cover;
}
.about__hidden {
  opacity: 0;
  transform: translateY(9rem);
  transition: all 350ms ease-in-out;
}
.about__text {
  grid-area: about;
}
.about__profile {
  grid-area: profile;
}

.services {
  padding: 20px 19px 140px;
}
.services__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 294px;
  justify-items: center;
  align-items: center;
  grid-gap: 22px;
}
@media screen and (max-width: 768px) {
  .services__container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 576px) {
  .services__container {
    grid-template-columns: 1fr;
  }
}
.services__images {
  width: 205px;
  height: 116px;
}
.services__service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.skills {
  position: relative;
  padding: 0 18px;
  grid-area: skill;
}
.skills__container {
  display: flex;
  flex-direction: row;
  gap: 20px;
}
.skills__tech, .skills__tools {
  display: flex;
  flex-direction: column;
  justify-self: center;
  width: 100%;
}
.skills__names {
  display: grid;
  grid-template-columns: 45px 1fr;
  align-items: center;
  width: 100%;
  font-weight: 500;
  text-align: left;
  padding: 10px;
  margin-bottom: 15px;
  box-shadow: 0 0 6px 2px rgba(48, 48, 48, 0.1);
}
.skills__names img {
  width: 41px;
  height: 45px;
}
.skills__names .skill__name {
  font-size: clamp(11px, 3vw, 12px);
}

.projects {
  padding: 170px 28px;
}
.projects__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 720px;
}
.projects__project {
  margin-bottom: 70px;
}
.projects__header, .projects__link {
  display: flex;
  align-items: center;
  gap: 12px;
}
@media screen and (max-width: 500px) {
  .projects__header, .projects__link {
    flex-direction: column;
    align-items: flex-start;
  }
}
.projects__header, .projects__desc {
  margin-bottom: 15px;
}
.projects__link {
  display: flex;
  gap: 7px;
}
.projects__info {
  margin-bottom: 35px;
}
.projects__tools span {
  color: #fff;
  background-color: var(--red);
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  -webkit-border-radius: 0.25rem;
  border-radius: 0.25rem;
}
.projects__desc {
  font-size: clamp(14px, 2vw, 15px);
  line-height: 24px;
}
.projects__title {
  font-size: clamp(18px, 3vw, 21px);
  font-weight: 700;
}

.project {
  position: relative;
  overflow: hidden;
  height: 340px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  max-width: 60%;
  width: 100%;
  margin-left: auto;
}
@media screen and (max-width: 768px) {
  .project {
    max-width: 100%;
  }
}
@media screen and (max-width: 568px) {
  .project {
    height: 280px;
  }
}
@media screen and (max-width: 411px) {
  .project {
    height: 239px;
  }
}
.project::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform 500ms ease-in;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: right;
}
.project::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), transparent);
  z-index: -1;
}
.project--1::before {
  background-image: url("../../../images/D-anime.png");
}
.project--2::before {
  background-image: url("../../../images/ama.png");
}
.project--3::before {
  background-image: url("../../../images/Triumph.png");
}
.project--4::before {
  background-image: url("../../../images/frontendmentor.png");
}
.project--5::before {
  background-image: url("../../../images/D-movie.png");
}
.project--6::before {
  background-image: url("../../../images/D-arch.png");
}
.project__view, .project__code {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 34px;
  font-size: 12px;
  transition: all 0.3s ease;
  border-radius: 7px;
}
@media screen and (max-width: 768px) {
  .project__view, .project__code {
    font-size: 11px;
    height: 28px;
  }
}
.project__code {
  background-color: #343a40;
  border-color: #343a40;
  color: var(--white);
  width: 180px;
  transition: all 0.3s ease;
}
@media screen and (max-width: 768px) {
  .project__code {
    width: 135px;
  }
}
.project__code:hover {
  background-color: #343a40c9;
}
.project__view {
  background-color: #868d94;
  border-color: #868d94;
  color: var(--white);
  transition: all 0.3s ease;
  width: 130px;
}
@media screen and (max-width: 768px) {
  .project__view {
    width: 90px;
  }
}
.project__view:hover {
  background-color: #868d94ce;
}
.project__link {
  display: flex;
  gap: 14px;
  margin-top: 16px;
}
.project__title {
  font-size: clamp(18px, 4vw, 30px);
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 6px;
}

.tools__container,
.tech__container {
  display: flex;
  gap: 20px;
}
@media screen and (max-width: 768px) {
  .tools__container,
.tech__container {
    flex-direction: column;
    gap: 0px;
  }
}

.footer {
  position: relative;
  background-color: var(--black);
  padding: 1rem 8px;
}
.footer__container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.footer__link {
  display: flex;
  align-items: center;
  gap: 14px;
}
.footer__link__icon {
  transition: all 0.3s ease-in;
}
.footer__link__icon:hover svg path {
  transition: all 0.3s ease-in;
  fill: var(--red);
}
.footer__link__icon svg path {
  fill: var(--white);
}
.footer__text {
  color: var(--white);
  margin-top: 7px;
  text-align: center;
  font-size: clamp(12px, 2vw, 13px);
}